ction .rating ul li i{font-size:19px;cursor:pointer}.movie_card .ui.star.rating .icon{color:rgba(255,255,255,0.5)}.movie_card .blur_back{position:absolute;top:0;z-index:1;height:100%;right:0;background-size:cover;border-radius:11px}@media screen and (min-width: 768px){.movie_header{width:60%}.movie_desc{width:50%}.info_section{background:linear-gradient(to right, #0d0d0c 50%, transparent 100%)}.blur_back{width:80%;background-position:-100% 10% !important}}@media screen and (max-width: 768px){.movie_card{width:95%;margin:70px auto;min-height:350px;height:auto}.blur_back{width:100%;background-position:50% 50% !important}.movie_header{width:100%;margin-top:85px}.movie_desc{width:100%}.info_section{background:linear-gradient(to top, #141413 50%, transparent 100%);display:inline-grid}}#bright{box-shadow:0px 0px 150px -45px rgba(255,51,0,0.5)}#bright:hover{box-shadow:0px 0px 120px -55px rgba(255,51,0,0.5)}

